import React from 'react';
import './WorkbooksTable.scss';
import { Link } from 'react-router-dom';

interface WorkbooksTableProps {
  content?: any[];
}

const WorkbooksTable: React.FC<WorkbooksTableProps> = (
  props: WorkbooksTableProps
) => {
  const { content } = props;
  return (
    <table className='WorkBooksTable'>
      <thead>
        <tr>
          <th>Title</th>
          <th>Owner</th>
          <th>Document Count</th>
          <th>Date</th>
        </tr>
      </thead>
      <tbody>
        {content!
          .map(i => (
            <tr key={i.id}>
              <td>
                <Link
                  to={{
                    pathname: `/workbooks/${i.id}`,
                    state: { name: i.name },
                  }}
                >
                  {i.name}
                </Link>
              </td>
              <td>{i.owner}</td>
              <td>{i.doctotal}</td>
              <td>{i.date}</td>
            </tr>
          ))
          .slice(20)}
      </tbody>
    </table>
  );
};

export default WorkbooksTable;
