import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import UserWidget from 'components/UserWidget';
import './Header.scss';

interface HeaderProps {
  signout: () => void;
}

const menuItems: string[] = ['one', 'two', 'three', 'four', 'five', 'six'];

const Header: React.FC<HeaderProps> = (props: HeaderProps) => {
  const [selected, setSelected] = useState({
    text: 'one',
    width: 56,
    left: 30,
  });

  const itemClick = (e: React.MouseEvent<HTMLElement>) => {
    const { textContent } = e.currentTarget;
    const { width } = e.currentTarget.getBoundingClientRect();
    const left = e.currentTarget.offsetLeft;
    setSelected({ text: textContent!, width, left });
  };

  const tabItems = (list: string[], selected: string) => {
    return list.map((i: string) => (
      <li
        onClick={itemClick}
        key={i}
        className={selected === i ? 'active' : ''}
      >
        <Link to={`/workbooks/${i}`}>{i}</Link>
      </li>
    ));
  };

  return (
    <div className='Header'>
      <div className='HeaderContent'>
        <ul className='tabMenu'>{tabItems(menuItems, selected.text)}</ul>
        <UserWidget signout={props.signout} />
      </div>
      <div
        className='activeMarker'
        style={{ left: selected.left, width: selected.width }}
      ></div>
    </div>
  );
};

export default Header;
