import React from 'react';
import './UserWidget.scss';

interface UserProps {
  signout?: (e: React.MouseEvent<HTMLElement>) => void;
}

const UserWidget: React.FC<UserProps> = (props: UserProps) => {
  return (
    <div className='UserWidget' onClick={props.signout}>
      <div className='UserWidgetAvatar' />
    </div>
  );
};

export default UserWidget;
