import React from 'react';
import Page from 'layouts/Page';
import WorkbooksTable from 'components/WorkbooksTable';
import { workbooks } from './workbooks';
import './Home.scss';

type workbook = {
  name: string;
  owner: string;
  docTotal: number;
  date: string;
};

interface IProps {
  title?: string;
  workbooks?: workbook[];
  signout?: () => void;
}

interface IState {}

class Home extends React.Component<IProps, IState> {
  static defaultProps = {
    title: 'Workbooks',
    workbooks,
  };

  render() {
    return (
      <Page signout={this.props.signout!} title={this.props.title}>
        <WorkbooksTable content={this.props.workbooks} />
      </Page>
    );
  }
}

export default Home;
