import React from 'react';
import Page from 'layouts/Page';
import './Workbook.scss';

type workbook = {
  name: string;
  owner: string;
  docTotal: number;
  date: string;
};

interface IProps {
  title?: string;
  workbooks?: workbook[];
  signout?: () => void;
  match?: any;
  location?: any;
}

interface IState {
  overlay: boolean;
}

class Workbook extends React.Component<IProps, IState> {
  static defaultProps = {
    title: 'Workbook',
  };

  filler = <div>{this.props.match.params.id}</div>;

  thumbs = ['One', 'Two', 'Three'];

  renderThumbs = (list: any) => {
    return list.map((i: string) => (
      <div className='doc' key={i}>
        <div className='thumb' />
        <div className='title'>{i}</div>
      </div>
    ));
  };

  showOverlay = (e: React.SyntheticEvent) => {
    this.setState({ overlay: true });
    console.log('overlay: true');
  };

  hideOverlay = (e: React.SyntheticEvent) => {
    this.setState({ overlay: false });
    console.log('overlay: false');
  };

  render() {
    const { name } = this.props.location.state || 'Workbook';
    return (
      <Page signout={this.props.signout!} title={name}>
        <div className='Workbook'>
          <div className='thumbs'>
            <div className='heading'>Documents</div>
            {this.renderThumbs(this.thumbs)}
          </div>
          <div className='pages'>
            <div className='heading'>Pages</div>
            <div className='page'></div>
            <div className='page'></div>
          </div>
          <div className='tools'>
            <div className='heading'>Tools</div>
            <div
              className='tool toggle'
              onMouseDown={this.hideOverlay}
              onMouseUp={this.showOverlay}
              onKeyDown={this.hideOverlay}
              onKeyUp={this.showOverlay}
            ></div>
          </div>
        </div>
      </Page>
    );
  }
}

export default Workbook;
